import React from 'react';
import PropTypes from 'prop-types';
import { extractChartReadyData, createChart } from '@splunk/charting';

export default class SplunkChart extends React.Component {
  static propTypes = {
    /*
      fields are aggregations.
      example: Avg, Max, Min, StdDev etc...

      columns are array of values for each aggregation. The first column is always
      _time (s).

      [['152024000', '152074000', ...], ['1', '2', ...], ['10', '20', ...]]
    */
    chartData: PropTypes.shape({
      fields: PropTypes.arrayOf(PropTypes.string),
      columns: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.string)),
    }),

    properties: PropTypes.shape({
      chart: PropTypes.string,
    }),

    refID: PropTypes.string,

    /**
     * style for the chart contianer
     */
    style: PropTypes.object,
  };

  static defaultProps = {
    chartData: {
      fields: [],
      columns: [],
    },
    properties: {
      chart: '',
    },
    refID: 'container',
    style: {
      position: 'relative',
      overflow: 'hidden',
      width: '100%',
      height: '250px',
    },
  }

  componentWillReceiveProps(nextProps) {
    const container = document.getElementById(this.props.refID);
    // remove exisiting chart
    while (container.firstChild) {
      container.removeChild(container.firstChild);
    }

    if (nextProps.chartData) {
      const dataSet = extractChartReadyData(nextProps.chartData);
      const chart = createChart(container, nextProps.properties);
      chart.prepareAndDraw(
        dataSet,
        nextProps.properties,
      );
    }
  }

  componentWillUnmount() {
    if (this.chart) {
      this.chart.destroy();
    }
  }

  render() {
    return (
      <div id={this.props.refID} style={this.props.style} />
    );
  }
}



