import React from 'react';
import { pick } from 'lodash';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import About from '../../components/settings/About';
import { loadAboutInfo } from '../../actions/settings/AboutInfoRetrieve';

class AboutInfoContainer extends React.Component {
  static propTypes = { ...About.propTypes }
  static defaultProps = { ...About.defaultProps }

  componentDidMount() {
    this.props.onLoadAboutInfo();
  }
  render() {
    const aboutProps = {
      ...pick({ ...this.props, ...this.state }, Object.keys(About.propTypes)),
    };
    return (
      <div>
        <About {...aboutProps} />
      </div>
    );
  }
}

const mapStatesToProps = state => ({
  ...state.aboutInfoReducer,
});

const mapDispatchToProps = dispatch => bindActionCreators({
  onLoadAboutInfo: loadAboutInfo,
}, dispatch);

export default connect(
  mapStatesToProps,
  mapDispatchToProps,
)(AboutInfoContainer);



