import { put, takeLatest, call } from 'redux-saga/effects';
import { get } from 'lodash';
import getSearchResult from '../services/about';
import { ABOUT_INFO_RETRIEVE } from '../actions/actionTypes';
import { aboutInfoRetrieveTracker } from '../actions/settings/AboutInfoRetrieve';
import { getAppBuildVersion } from '../util/SearchConstants';

/**
* Helper function to parse the response 
*/
function parseResponse(response) {
  /** The result should contain two rows, one for infrastructure app, one for aws app */
  if (response && response.results && response.results.length > 1) {
    return {
      /* sort on title, would make sure first row has app version */
      appVersion: get(response.results[0], 'version', 'n/a'),
      appBuild: get(response.results[0], 'build', 'n/a'),
      appServer: get(response.results[0], 'serverName', 'n/a'),
      awsVersion: get(response.results[1], 'version', 'n/a'),
    };
  }
  return {};
}

export function* retrieveAboutInfo() {
  yield put(aboutInfoRetrieveTracker.start());
  try {
    const { response, error } = yield call(getSearchResult, getAppBuildVersion);
    if (response && response.results && response.results.length > 1) {
      const aboutInfo = parseResponse(response);
      yield put(aboutInfoRetrieveTracker.success(aboutInfo));
    } else {
      yield put(aboutInfoRetrieveTracker.failure(error));
    }
  } catch (error) {
    yield put(aboutInfoRetrieveTracker.failure(error));
  }
}

export default function* aboutInfoSaga() {
  yield takeLatest(ABOUT_INFO_RETRIEVE, retrieveAboutInfo);
}




