import React from 'react';
import PropTypes from 'prop-types';
import Heading from '@splunk/react-ui/Heading';
import DL from '@splunk/react-ui/DefinitionList';
import Link from '@splunk/react-ui/Link';
import Paragraph from '@splunk/react-ui/Paragraph';
import { buildNumber, versionLabel } from '@splunk/splunk-utils/config';
import { _ } from '@splunk/ui-utils/i18n';
import { createTestHook } from '../../util/TestSupport';
import { TRADEMARKS, PATENTS, COPYRIGHT } from '../../util/MessageConstants';

export default class About extends React.Component {
  static propTypes = {
    /** version of waitomo */
    appVersion: PropTypes.string,
    /** build of waitomo */
    appBuild: PropTypes.string,
    /** the name of server  */
    appServer: PropTypes.string,
    /** splunk_TA_aws version */
    awsVersion: PropTypes.string,
  };

  static defaultProps = {
    appVersion: '',
    appBuild: '',
    appServer: '',
    awsVersion: '',
  };

  constructor(props) {
    super(props);
    this.styles = {
      main: {
        marginLeft: '15px',
      },
      paragraph: {
        maxWidth: '720px',
      },
      thirdParty: {
        paddingTop: '30px',
        paddingBottom: '20px',
      },
      wrapper: {
        overflow: 'auto',
      },
    };
  }


  render() {
    return (
      <div style={this.styles.main} {...createTestHook(__filename, name)}>
        <Heading level={2}>{_('Splunk Insights For Infrastructure')}</Heading>
        <div style={this.styles.wrapper}>
          <DL style={{ float: 'left' }}>
            <DL.Term>{_('Version')}</DL.Term>
            <DL.Description data-test-label="appVersion">{this.props.appVersion}</DL.Description>
            <DL.Term>{_('Build')}</DL.Term>
            <DL.Description data-test-label="appBuild">{this.props.appBuild}</DL.Description>
            <DL.Term>{_('Server')}</DL.Term>
            <DL.Description data-test-label="appServer">{this.props.appServer}</DL.Description>
          </DL>
        </div>
        <DL style={{ marginTop: '30px' }} termWidth={250}>
          <DL.Term>{_('Splunk TA AWS Version')}</DL.Term>
          <DL.Description data-test-label="awsVersion">{this.props.awsVersion}</DL.Description>
          <DL.Term>{_('Splunk Version')}</DL.Term>
          <DL.Description data-test-label="splunkVersion">{versionLabel}</DL.Description>
          <DL.Term>{_('Splunk Build')}</DL.Term>
          <DL.Description data-test-label="splunkBuild">{buildNumber}</DL.Description>
        </DL>
        <div style={this.styles.thirdParty}>
          <Link
            openInNewContext
            to="http://docs.splunk.com/Documentation/Infrastructure/1.0.0/ReleaseNotes/Credits"
          >
            {_('Third-Party Software Credits and Attribution')}
          </Link>
        </div>
        <Heading
          level={2}
        >
          {_('Trademarks')}
        </Heading>
        <Paragraph
          style={this.styles.paragraph}
        >
          {_(TRADEMARKS)}
        </Paragraph>
        <Heading
          level={2}
        >
          {_('Patents')}
        </Heading>
        <Paragraph
          style={this.styles.paragraph}
        >
          {_(PATENTS)}
          <Link
            openInNewContext
            to="https://www.splunk.com/en_us/legal/patents.html"
          >
            {_(' here.')}
          </Link>
        </Paragraph>
        <Paragraph
          style={this.styles.paragraph}
        >
          {_(COPYRIGHT)}
          <Link
            openInNewContext
            to="https://www.splunk.com/en_us/legal/splunk-software-license-agreement.html"
          >
            {_(' Splunk Software License Agreement.')}
          </Link>
        </Paragraph>
      </div>
    );
  }
}



