import React from 'react';
import PropTypes from 'prop-types';
import { _ } from '@splunk/ui-utils/i18n';
import Button from '@splunk/react-ui/Button';
import Heading from '@splunk/react-ui/Heading';
import ConfirmationDialog from '../../components/common/ConfirmationDialog';
import { FETCH_OBJECT_STATE } from '../../util/StateConstants';

export default class ServerLogging extends React.Component {
  static propTypes = {
    /** Restart state */
    restartState: PropTypes.string,
    /** restart actions */
    restartSplunk: PropTypes.func.isRequired,
    /** load user roles to check if the user can restart */
    loadUserRoles: PropTypes.func.isRequired,
    /** canRestart boolean */
    canRestart: PropTypes.bool.isRequired,
  }

  static defaultProps = {
    restartState: FETCH_OBJECT_STATE.READY,
    canRestart: false,
  }

  constructor() {
    super();

    this.styles = {
      annotation: {
        fontSize: '12px',
        color: 'rgb(107, 119, 133)',
        marginLeft: '15px',
        marginTop: '4px',
      },
      bold: {
        fontWeight: 'bold',
      },
      main: {
        margin: '15px',
      },
      restartSplunk: {
        marginLeft: '20px',
      },
      serverName: {
        margin: '15px',
        marginLeft: '30px',
      },
      wrapper: {
        marginLeft: '30px',
        marginTop: '20px',
      },
    };

    this.state = {
      open: false,
      restartInProgress: false,
    };
    this.onRestartClick = this.onRestartClick.bind(this);
  }

  componentDidMount() {
    this.props.loadUserRoles();
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.restartState === FETCH_OBJECT_STATE.IN_PROGRESS) {
      this.setState({
        restartInProgress: true,
      });
    }

    // Incase the rest endpoint failed
    if (nextProps.restartState === FETCH_OBJECT_STATE.ERROR) {
      this.setState({
        restartInProgress: false,
      });
    }
  }

  onRestartClick() {
    this.setState({
      open: true,
    });
  }

  onRestartClicked() {
    this.setState({
      restartInProgress: true,
    });
    this.props.restartSplunk();
  }

  isRestartInProgress() {
    return this.props.restartState === FETCH_OBJECT_STATE.IN_PROGRESS ||
     this.state.restartInProgress;
  }

  render() {
    return (
      <div style={this.styles.main}>
        <Heading level={2}>{_('Server Logging')}</Heading>
        <div style={this.styles.annotation}>
          {_('View internal log data to troubleshoot an issue with Splunk Insights for Infrastructure or restart Splunk.')}
        </div>
        <div style={this.styles.serverName}>
          <span style={this.styles.bold}> {_('Server Name:')} </span>
          <span> {_(`${window.location.hostname}`)} </span>
        </div>
        <div style={this.styles.wrapper}>
          <Button
            openInNewContext
            to="metrics_analysis?ts"
            label={_('Investigate')}
          />
          <Button
            onClick={this.onRestartClick}
            label={_('Restart Splunk')}
            disabled={!this.props.canRestart}
            style={this.styles.restartSplunk}
          />
          <ConfirmationDialog
            showCancel
            hideCloseButton
            open={this.state.open}
            title={_('Restart Splunk')}
            primaryButtonText={_('Restart')}
            primaryButtonClicked={() => this.onRestartClicked()}
            disablePrimaryClick={this.state.restartInProgress}
            closeOnClick={false}
            closeOnClickAway={false}
          >
            {this.isRestartInProgress() ?
              _('Restart in progress...') : _('Are you sure you want to restart Splunk?')}
          </ConfirmationDialog>
        </div>
      </div>
    );
  }
}




