import React from 'react';
import PropTypes from 'prop-types';
import Heading from '@splunk/react-ui/Heading';
import Text from '@splunk/react-ui/Text';
import RadioList from '@splunk/react-ui/RadioList';
import Button from '@splunk/react-ui/Button';
import ControlGroup from '@splunk/react-ui/ControlGroup';
import WaitSpinner from '@splunk/react-ui/WaitSpinner';
import Message from '@splunk/react-ui/Message';
import { each } from 'lodash';
import { _ } from '@splunk/ui-utils/i18n';
import { createTestHook } from '../../util/TestSupport';
import { EMAIL_SETTINGS_STATUS, EMAIL_SECURITY_MODE } from '../../util/StateConstants';

export default class EmailSettings extends React.Component {
  static propTypes = {
    /**
     *Mail host
     */
    mailserver: PropTypes.string,
    /**
     *  mail security mode
     */
    securityMode: PropTypes.oneOf(Object.values(EMAIL_SECURITY_MODE)),
    /**
     *  Username 
     */
    authUsername: PropTypes.string,
    /**
    *   Password
    */ 
    authPassword: PropTypes.string,
    /**
    *   confirmPassword
    */ 
    confirmPassword: PropTypes.string,
    /**
     * load/update state for email setting
     */
    emailSettingStatus: PropTypes.oneOf(Object.values(EMAIL_SETTINGS_STATUS)),
    /**
     * callback when click submit email settings
     */
    onSubmitEmailChanges: PropTypes.func,
    /**
     * callback when email settings content is changed
     */
    onUpdateEmailSettings: PropTypes.func,

  };

  static defaultProps = {
    mailserver: 'localhost',
    securityMode: 1,
    authUsername: '',
    authPassword: '',
    confirmPassword: '',
    emailSettingStatus: EMAIL_SETTINGS_STATUS.READY,
    onSubmitEmailChanges: null,
    onUpdateEmailSettings: null,
  };

  // TODO: need to check unused styles
  constructor(props) {
    super(props);
    this.styles = {
      controls: {
        marginLeft: '20px',
      },
      heading: {
        marginTop: '20px',
        marginLeft: '15px',
        marginBottom: '20px',
      },
      loadingText: {
        fontSize: 12,
        fontStyle: 'italic',
        verticalAlign: 'middle',
      },
      loadSpinner: {
        paddingRight: '5px',
        marginLeft: '10px',
        verticalAlign: 'middle',
      },
      radioOption: {
        marginLeft: '1em',
      },
      radios: {
        display: 'flex',
        flexDirection: 'row',
      },
      updateEmail: {
        marginLeft: '140px',
      },
    };
    this.state = {
      emailErrorStates: {
        mailserver: false,
        authUsername: false,
        authPassword: false,
        confirmPassword: false,
      },
    };
  }

  getFormattedWaitSpinner = () => (
    <span>
      <WaitSpinner size="medium" style={this.styles.loadSpinner} />
      <span style={this.styles.loadingText} className="loading-text">
        {_('Update Email Settings, Please Wait')}
      </span>
    </span>
  )

  getEmailSettingStatus = (accountState) => {
    let statusIndicator;
    switch (accountState) {
      case EMAIL_SETTINGS_STATUS.IN_PROGRESS:
        statusIndicator = this.getFormattedWaitSpinner();
        break;

      case EMAIL_SETTINGS_STATUS.SUCCESS:
        statusIndicator = (
          <Message
            type="success"
            style={{ marginLeft: '135px' }}
            data-test="email-setting-success-message"
          >
            {_('Email settings saved.')}
          </Message>
        );
        break;

      case EMAIL_SETTINGS_STATUS.ERROR:
        statusIndicator = (
          <Message
            type="error"
            style={{ marginLeft: '135px' }}
            data-test="email-setting-error-message"
          >
            {_('Error: Could not update email settings.')}
          </Message>
        );
        break;

      default:
        break;
    }
    return statusIndicator;
  };

  handleMailSettingsSubmit = () => {
    let allowSubmit = true;
    let newEmailErrorStates = this.state.emailErrorStates;
    each(this.state.emailErrorStates, (value, key) => {
      // Do not allow empty strings, show error state
      if (!this.props[key]) {
        newEmailErrorStates = { ...newEmailErrorStates, [key]: true };
        allowSubmit = false;
      }
    });
    if (this.props.authPassword !== this.props.confirmPassword) {
      newEmailErrorStates.confirmPassword = true;
      allowSubmit = false;
    }
    if (allowSubmit) {
      this.props.onSubmitEmailChanges();
    } else {
      this.setState({ emailErrorStates: newEmailErrorStates });
    }
  };

  handleFormFieldChange = (e, { value, name }) => {
    if (name !== undefined && name in this.props) {
      const newErrorState = { ...this.state.emailErrorStates, [name]: false };
      this.setState({ emailErrorStates: newErrorState });
      this.props.onUpdateEmailSettings(name, value);
    }
  };

  render() {
    return (
      <div {...createTestHook(__filename, name)}>
        <Heading style={this.styles.heading} level={2}>{_('Email Server Settings')}</Heading>
        <div
          style={this.styles.controls}
        >
          <ControlGroup
            label={_('Mail host')}
            error={this.state.emailErrorStates.mailserver}
            help={this.state.emailErrorStates.mailserver ? _('Required Field.') : _('Set the host that sends mail for this Splunk instance.')}
          >
            <Text
              name={'mailserver'}
              error={this.state.emailErrorStates.mailserver}
              value={this.props.mailserver}
              onChange={this.handleFormFieldChange}
            />
          </ControlGroup>
          <ControlGroup
            label={_('Email security')}
            help={_('Check with SMTP server admin. When SSL is enabled, mail host should include the port. IE: smtp.splunk.com:465')}
          >
            <RadioList name={'securityMode'} style={this.styles.radios} value={this.props.securityMode} onChange={this.handleFormFieldChange} >
              <RadioList.Option value={1}>{_('none')}</RadioList.Option>
              <RadioList.Option value={2} style={this.styles.radioOption}>{_('Enable SSL')}</RadioList.Option>
              <RadioList.Option value={3} style={this.styles.radioOption}>{_('Enable TLS')}</RadioList.Option>
            </RadioList>
          </ControlGroup>
          <ControlGroup
            label={_('Username')}
            error={this.state.emailErrorStates.authUsername}
            help={this.state.emailErrorStates.authUsername ? _('Required Field.') : _('Username to use when authenticating with the SMTP server. Leave empty for no authentication.')}
          >
            <Text
              name={'authUsername'}
              error={this.state.emailErrorStates.authUsername}
              value={this.props.authUsername}
              onChange={this.handleFormFieldChange}
            />
          </ControlGroup>
          <ControlGroup
            label={_('Password')}
            error={this.state.emailErrorStates.authPassword}
            help={this.state.emailErrorStates.authPassword ? _('Required Field.') : _('Password to use when authenticating with the SMTP server')}
          >
            <Text
              name={'authPassword'}
              type="password"
              error={this.state.emailErrorStates.authPassword}
              value={this.props.authPassword}
              onChange={this.handleFormFieldChange}
            />
          </ControlGroup>
          <ControlGroup
            label={_('Confirm Password')}
            error={this.state.emailErrorStates.confirmPassword}
            help={this.state.emailErrorStates.confirmPassword ? _('Password does not match.') : ''}
          >
            <Text
              name={'confirmPassword'}
              type="password"
              error={this.state.emailErrorStates.confirmPassword}
              value={this.props.confirmPassword}
              onChange={this.handleFormFieldChange}
            />
          </ControlGroup>
          <Button
            name={'updateEmail'}
            label={_('Update Email Server Settings')}
            onClick={this.handleMailSettingsSubmit}
            style={this.styles.updateEmail}
          />
          { this.getEmailSettingStatus(this.props.emailSettingStatus) }
        </div>
      </div>
    );
  }
}



