import React from 'react';
import TabLayout from '@splunk/react-ui/TabLayout';
import ServerLogging from '../../containers/settings/ServerLogging';
import EmailSettingsContainer from '../../containers/settings/EmailSettings';
import Instrumentation from '../../containers/settings/Instrumentation';
import LicensingContainer from '../../containers/settings/Licensing';
import AboutInfoContainer from '../../containers/settings/AboutInfo';
import { isEnterprise } from '../../util/Common';
import { createTestHook } from '../../util/TestSupport';

export default class SettingsPageContents extends React.Component {
  constructor(props, ...rest) {
    super(props, ...rest);

    this.styles = {
      main: {
        margin: 'auto auto',
        paddingTop: '20px',
        paddingLeft: '20px',
      },
    };
  }

  render() {
    return (
      <div {...createTestHook(__filename, name)}>
        <TabLayout
          defaultActivePanelId="serverSettings"
          tabWidth={150}
          layout="vertical"
          style={{ backgroundColor: 'white' }}
        >
          <TabLayout.Panel label="Server settings" panelId="serverSettings">
            <ServerLogging />
            <EmailSettingsContainer />
          </TabLayout.Panel>
          <TabLayout.Panel label="Licensing" panelId="licensing">
            <LicensingContainer />
          </TabLayout.Panel>
          { !isEnterprise() ?
            <TabLayout.Panel label="Instrumentation" panelId="instrumentation">
              <Instrumentation />
            </TabLayout.Panel>
            : null }
          <TabLayout.Panel label="About" panelId="about">
            <AboutInfoContainer />
          </TabLayout.Panel>
        </TabLayout>
      </div>
    );
  }
}



