import { cloneDeep } from 'lodash';
import React from 'react';
import PropTypes from 'prop-types';
import Button from '@splunk/react-ui/Button';
import ColumnLayout from '@splunk/react-ui/ColumnLayout';
import File from '@splunk/react-ui/File';
import Heading from '@splunk/react-ui/Heading';
import Link from '@splunk/react-ui/Link';
import Message from '@splunk/react-ui/Message';
import Modal from '@splunk/react-ui/Modal';
import Table from '@splunk/react-ui/Table';
import WaitSpinner from '@splunk/react-ui/WaitSpinner';
import { _ } from '@splunk/ui-utils/i18n';
import { parseEpochToDate } from '../../util/Common';
import { createTestHook } from '../../util/TestSupport';
import {
  EXPIRED_LICENSE_ERROR,
  NONLITE_LICENSE_ERROR,
} from '../../util/MessageConstants';
import {
  EDIT_OBJECT_STATE,
  FETCH_OBJECT_STATE,
  LICENSE_ERRORS,
} from '../../util/StateConstants';
import SplunkChart from '../common/SplunkChart';

const GB_CONVERSION = 1024 * 1024 * 1024;
export default class Licensing extends React.Component {
  static propTypes = {
    licenseName: PropTypes.string,
    retentionVol: PropTypes.number,
    expiredDate: PropTypes.string,
    licenseFetchState: PropTypes.oneOf(Object.values(FETCH_OBJECT_STATE)),
    licenseDailyUsageFetchState: PropTypes.oneOf(Object.values(FETCH_OBJECT_STATE)),
    licenseUploadError: PropTypes.object,
    licenseUploadState: PropTypes.oneOf(Object.values(EDIT_OBJECT_STATE)),
    // Callback for resetting license upload (on modal close)
    onResetLicenseUpload: PropTypes.func,
    /**
     * callback when click upload license
     */
    onUploadLicenseFile: PropTypes.func,
    /*
      include props of SplunkChart
    */
    ...SplunkChart.propTypes,

    dailyChartData: PropTypes.shape({
      fields: PropTypes.arrayOf(PropTypes.string),
      columns: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.string)),
    }),

    sumChartData: PropTypes.shape({
      fields: PropTypes.arrayOf(PropTypes.string),
      columns: PropTypes.arrayOf(PropTypes.arrayOf(PropTypes.string)),
    }),

    dailyUsage: PropTypes.number,
  };

  static defaultProps = {
    licenseName: 'n/a',
    retentionVol: null,
    expiredDate: null,
    licenseFetchState: FETCH_OBJECT_STATE.READY,
    licenseDailyUsageFetchState: FETCH_OBJECT_STATE.READY,
    licenseUploadError: {},
    licenseUploadState: EDIT_OBJECT_STATE.READY,
    onResetLicenseUpload: null,
    onUploadLicenseFile: null,
    /*
      include default props of Chart
    */
    ...SplunkChart.defaultProps,

    dailyChartData: {
      fields: [],
      columns: [],
    },

    sumChartData: {
      fields: [],
      columns: [],
    },

    dailyUsage: 0,
  };

  constructor(props) {
    super(props);
    this.styles = {

      heading: {
        float: 'left',
      },
      titleFont: {
        color: '#3C444D',
        fontWeight: 600,
        paddingLeft: '15px',
        marginTop: '10px',
      },
      loadingText: {
        fontSize: 12,
        fontStyle: 'italic',
        verticalAlign: 'middle',
      },
      loadSpinner: {
        paddingRight: '5px',
        marginLeft: '10px',
        verticalAlign: 'middle',
      },
      upload: {
        maxWidth: '300px',
        marginLeft: '20px',
        marginTop: '20px',
      },
      leftUpperCell: {
        background: ' #ffffff',
        marginLeft: '15px',
        marginRight: '5px',
        marginTop: '15px',
      },
      rightUpperCell: {
        background: ' #ffffff',
        marginLeft: '5px',
        marginRight: '15px',
        marginTop: '15px',
      },
      leftBottomCell: {
        background: ' #ffffff',
        marginLeft: '15px',
        marginRight: '5px',
      },
      rightBottomCell: {
        background: ' #ffffff',
        marginRight: '15px',
        marginLeft: '5px',
      },
      licTable: {
        background: ' #ffffff',
        marginLeft: '15px',
        minHeight: '80px',
        marginRight: '15px',
        marginBottom: '20%',
      },
      layout: {
        background: '#eee',
      },
      singleValue: {
        fill: 'rgb(51, 51, 51)',
        opacity: 1,
        textAlign: 'center',
        verticalAlign: 'middle',
        marginTop: '15%',
      },
      description: {
        marginTop: '20px',
        fontSize: '14px',
        textAlign: 'center',
        fontWeight: 'regular',
      },
      container: {
        display: 'flex',
        flexDirection: 'row wrap',
      },
      button: {
        paddingTop: '10px',
        paddingRight: '10px',
        float: 'right',
      },
      buttonFont: {
        color: '#006eaa',
        fontSize: 12,
      },
      largeText: {
        fontSize: '60px',
      },
      infinityIcon: {
        fontSize: '120px',
      },
      smallText: {
        fontSize: '24px',
        fontWeight: 'bold',
      },
      bottomTable: {
        marginLeft: '23px',
        marginRight: '23px',
        marginBottom: '25px',
      },
      gaugeStyle: {
        position: 'relative',
        overflow: 'hidden',
        width: '100%',
        height: '100px',
      },
    };
    this.state = {
      modalOpen: false,
    };

    this.fileReader = new FileReader();
  }

  getFormattedWaitSpinner = () => (
    <span>
      <WaitSpinner size="medium" style={this.styles.loadSpinner} />
      <span style={this.styles.loadingText} className="loading-text">
        {_('Uploading license, Please Wait')}
      </span>
    </span>
  )

  getChartWaitSpinner = () => (
    <span>
      <WaitSpinner size="medium" style={this.styles.loadSpinner} />
      <span style={this.styles.loadingText} className="loading-text">
        {_('Calculating daily data ingestion, Please Wait')}
      </span>
    </span>
  )

  getLicenseUploadModal = () => {
    if (!this.state.modalOpen) {
      return (null);
    }

    // Licensing error modal
    const modalContents = [];
    let specialError = '';

    // Convert error status into special error string
    const licenseUploadError = this.props.licenseUploadError;
    if (licenseUploadError) {
      if (licenseUploadError.status === LICENSE_ERRORS.NONLITE) {
        specialError = _(NONLITE_LICENSE_ERROR);
      } else if (licenseUploadError.status === LICENSE_ERRORS.EXPIRED) {
        const expirationDate = parseEpochToDate(Number(licenseUploadError.message));
        specialError = `${_(EXPIRED_LICENSE_ERROR)}${_(expirationDate)}`;
      }
    }

    if (specialError) {
      modalContents.push(<br key={'br_initial'} />);
    }

    // Replace new line characters with line breaks
    specialError.split('\n').forEach((elem, index) => {
      // eslint-disable-next-line react/no-array-index-key
      modalContents.push(<br key={`br_${index}`} />);
      modalContents.push(_(elem));
    });

    if (licenseUploadError.status === LICENSE_ERRORS.NONLITE) {
      modalContents.push(<br key={'br_link'} />);
      modalContents.push(
        <Link
          to="http://docs.splunk.com/Documentation/Infrastructure/1.0.0/Admin/Upgrade"
          openInNewContext
          name={'errorLink'}
          key={'modal_link'}
        >
          {_('How to upgrade')}
        </Link>,
      );
    }

    return (
      <Modal
        open
        onRequestClose={this.handleCloseUploadModal}
        style={{ width: '600px' }}
      >
        <Modal.Header
          title={'Upload License'}
          onRequestClose={this.handleCloseUploadModal}
        />
        <Modal.Body>
          {_('To install a license, drop or upload a license file here (license files end with .lic):')}
          <br />
          { this.getLicenseState(this.props.licenseUploadState, modalContents) }
          <div style={this.styles.upload}>
            <File
              onRequestAdd={this.handleAddFiles}
              onRequestRemove={this.handleRemoveFile}
            >
              {this.state.filename &&
                <File.Item
                  name={this.state.filename}
                  error={'status' in this.props.licenseUploadError}
                />}
            </File>
          </div>
          <br />
          <div>
            {_('Don\'t have a license yet?')}
            <Link
              to="http://docs.splunk.com/Documentation/Infrastructure/1.0.0/Admin/AboutLicensing"
              openInNewContext
              style={{ marginLeft: '5px' }}
              name={'licenseOptions'}
            >
              {_('Find out more here')}
            </Link>
          </div>
        </Modal.Body>
        <Modal.Footer>
          <Button
            name={'cancelModal'}
            label={_('Cancel')}
            onClick={this.handleCloseUploadModal}
          />
          <Button
            name={'uploadLicense'}
            label={_('Upload')}
            onClick={this.handleUploadLicense}
            style={{ marginLeft: '10px' }}
            appearance={'primary'}
          />
        </Modal.Footer>
      </Modal>
    );
  };

  getLicenseState = (licenseState, errorContents) => {
    let statusIndicator;
    switch (licenseState) {
      case EDIT_OBJECT_STATE.IN_PROGRESS:
        statusIndicator = this.getFormattedWaitSpinner();
        break;

      case EDIT_OBJECT_STATE.SUCCESS:
        statusIndicator = (
          <div>
            <br />
            <Message
              type="success"
              data-test="license-success-message"
            >
              {_('Success: Increased license to ') + (this.props.retentionVol / GB_CONVERSION) + _(' GB with expiration of ') + this.props.expiredDate}
            </Message>
          </div>
        );
        break;

      case EDIT_OBJECT_STATE.ERROR:
        statusIndicator = (
          <div>
            <br />
            <Message
              type="error"
              data-test="license-error-message"
            >
              {_('Error: Failed to upload license')}
              {errorContents}
            </Message>
          </div>
        );
        break;

      default:
        break;
    }
    return statusIndicator;
  };

  getDailyUsageState = (usageState) => {
    let statusIndicator;
    switch (usageState) {
      case FETCH_OBJECT_STATE.IN_PROGRESS:
        statusIndicator = this.getChartWaitSpinner();
        break;

      case FETCH_OBJECT_STATE.ERROR:
        statusIndicator = (
          <div>
            <br />
            <Message
              type="error"
              data-test="license-error-message"
            >
              {_('Error: Failed to calculate daily data ingestion')}
            </Message>
          </div>
        );
        break;

      default:
        break;
    }
    return statusIndicator;
  };

  getChartDataRounded = () => {
    // Verify that data is formatted correctly for further operations
    if (!this.props.chartData || !this.props.chartData.columns ||
      !this.props.chartData.columns[1] || !this.props.chartData.columns[1][0]) {
      return this.props.chartData;
    }
    // Round the chartData percentage to the nearest integer
    const rv = cloneDeep(this.props.chartData);
    const total = rv.columns[1].reduce((sum, elem) => Number(sum) + Number(elem));
    rv.columns[1] = rv.columns[1].map(
      elem => String((Math.round((Number(elem) / total) * 100) / 100) * total),
    );
    return rv;
  };

  getSumChartDataRounded = () => {
    // Verify that data is formatted correctly for further operations
    if (!this.props.sumChartData || !this.props.sumChartData.columns
      || isNaN(this.props.sumChartData.columns[0])) {
      return this.props.sumChartData;
    }
    // Round the sumChartData percentage to the nearest integer
    const rv = cloneDeep(this.props.sumChartData);
    const numerator = Number(rv.columns[0]);
    const denominator = Number(this.props.retentionVol);
    // Convert the numerator to the number that represents the closest rounded
    // integer percentage
    rv.columns[0] = [
      String((Math.round((numerator / denominator) * 100) / 100) * denominator),
    ];
    return rv;
  };

  getTotalUsage = () => {
    // Verify that data is formatted correctly for further operations
    if (!this.props.sumChartData || !this.props.sumChartData.columns ||
      isNaN(this.props.sumChartData.columns[0])) {
      return 0;
    }
    return (this.props.sumChartData.columns[0] / 1024).toFixed(2);
  };

  getRetentionText = () => {
    if (this.getTotalUsage() < this.props.retentionVol) {
      return _('All data is retained until storage is full');
    }
    return _('based on current ingestion rate.');
  };

  calculateRetentionDays = () => {
    if (this.getTotalUsage() < this.props.retentionVol) {
      return decodeURI('%E2%88%9E');
    } else if (isNaN(this.props.retentionVol) || isNaN(this.props.dailyUsage)) {
      return 0;
    }
    return (this.props.retentionVol / GB_CONVERSION / this.props.dailyUsage).toFixed(0);
  };

  handleOpenUploadModal = () => {
    this.setState({
      modalOpen: true,
    });
  };

  handleCloseUploadModal = () => {
    this.setState({
      filename: null,
      modalOpen: false,
    });
    this.props.onResetLicenseUpload();
  };

  handleAddFiles = (files) => {
    const file = files[0];
    if (this.fileReader.readyState === 1) {
      this.fileReader.abort();
    }

    this.fileReader.onload = () => {
      this.setState({
        filename: file.name,
        value: this.fileReader.result,
      });
    };

    this.fileReader.readAsText(file);
  };

  handleRemoveFile = () => {
    if (this.fileReader.readyState === 1) {
      this.fileReader.abort();
    }

    this.setState({
      filename: null,
      value: null,
    });
  };

  handleUploadLicense = () => {
    this.props.onUploadLicenseFile(this.state.filename, this.state.value);
  };

  render() {
    const pieProperties = {
      chart: 'pie',
      'chart.showDataLabels': 'all',
      'chart.showPercent': 'True',
    };
    const columnProperties = {
      chart: 'column',
      'chart.stackMode': 'stacked',
      'chart.showDataLabels': 'minmax',
      'axisTitleX.visibility': 'collapsed',
    };
    const gaugeProperties = {
      chart: 'fillerGauge',
      'chart.orientation': 'x',
      'chart.rangeValues': `[0, ${this.props.retentionVol}]`,
      'chart.usePercentageRange': 'True',
      'chart.usePercentageValue': 'True',
      'chart.showMinorTicks': 'False',
      'chart.showMajorTicks': 'False',
    };
    return (
      <div {...createTestHook(__filename, name)}>
        { this.getLicenseUploadModal() }
        <ColumnLayout gutter={8} style={this.styles.layout}>
          <ColumnLayout.Row>
            <ColumnLayout.Column span={6} style={this.styles.leftUpperCell}>
              <div >
                <div style={this.styles.heading}>
                  <Heading level={3} style={this.styles.titleFont}>{_('Total Storage Used')}</Heading>
                </div>
                <div style={this.styles.button}>
                  <Button
                    to="https://www.splunk.com/en_us/talk-to-sales/infrastructure-insights.html"
                    openInNewContext
                    appearance="pill"
                    label={_('Buy More Storage')}
                    style={this.styles.buttonFont}
                  />
                </div>
              </div>
              <div data-test="totalData" style={this.styles.singleValue}>
                <strong style={this.styles.largeText}>{this.getTotalUsage()}</strong> /
                {(this.props.retentionVol / GB_CONVERSION).toFixed(2)}
                <samp style={this.styles.smallText}>{_('  GB')}</samp>
                <SplunkChart chartData={this.getSumChartDataRounded()} properties={gaugeProperties} refID="sumData" style={this.styles.gaugeStyle} />
              </div>
            </ColumnLayout.Column>
            <ColumnLayout.Column span={6} style={this.styles.rightUpperCell}>
              <Heading level={3} style={this.styles.titleFont}>{_('Total Data Stored by Index')}</Heading>
              <SplunkChart chartData={this.getChartDataRounded()} properties={pieProperties} refID="totalData" />
            </ColumnLayout.Column>
          </ColumnLayout.Row>
          <ColumnLayout.Row>
            <ColumnLayout.Column span={6} style={this.styles.leftBottomCell}>
              <Heading level={3} style={this.styles.titleFont}>{_('Daily Data Ingestion per Data Type')}</Heading>
              {this.getDailyUsageState(this.props.licenseDailyUsageFetchState)}
              <SplunkChart chartData={this.props.dailyChartData} properties={columnProperties} refID="dailyData" />
            </ColumnLayout.Column>
            <ColumnLayout.Column span={6} style={this.styles.rightBottomCell}>
              <div style={{ height: '100%' }}>
                <div>
                  <Heading level={3} style={this.styles.titleFont}>{_('Days of Data Retention')}</Heading>
                </div>
                <div style={this.styles.singleValue} data-test="retentionDays">
                  <strong style={this.getTotalUsage() < this.props.retentionVol ? (
                    this.styles.infinityIcon
                  ) : (
                    this.styles.largeText
                  )}
                  >
                    {this.calculateRetentionDays()}
                  </strong><samp style={this.styles.smallText}>{_('  days')}</samp>
                </div>
                <div style={this.styles.description} >
                  {this.getRetentionText()}
                </div>
              </div>
            </ColumnLayout.Column>
          </ColumnLayout.Row>
          <ColumnLayout.Row>
            <ColumnLayout.Column span={12} style={this.styles.licTable}>
              <div >
                <Heading level={3} style={this.styles.titleFont}>{_('Current License')}</Heading>
                <div style={this.styles.bottomTable}>
                  <Table stripeRows >
                    <Table.Head>
                      <Table.HeadCell>License Name</Table.HeadCell>
                      <Table.HeadCell align="right">License Volume</Table.HeadCell>
                      <Table.HeadCell>Expired Date</Table.HeadCell>
                      <Table.HeadCell>Action</Table.HeadCell>
                    </Table.Head>
                    <Table.Body>
                      <Table.Row key={this.props.licenseName}>
                        <Table.Cell data-test-label="licenseName">{this.props.licenseName}</Table.Cell>
                        <Table.Cell align="right" data-test-label="retentionVol">{(this.props.retentionVol / GB_CONVERSION).toFixed(2)} GB</Table.Cell>
                        <Table.Cell data-test-label="expiredDate">{this.props.expiredDate}</Table.Cell>
                        <Table.Cell
                          name={'openUploadLicenseModal'}
                          onClick={this.handleOpenUploadModal}
                        >
                        Upload License
                        </Table.Cell>
                      </Table.Row>
                    </Table.Body>
                  </Table>
                </div>
              </div>
            </ColumnLayout.Column>
          </ColumnLayout.Row>
        </ColumnLayout>
      </div>
    );
  }
}



