import React from 'react';
import { pick } from 'lodash';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import ServerLogging from '../../components/settings/ServerLogging';
import { restartSplunk, loadUserRoles } from '../../actions/settings/ServerLogging';

class ServerLoggingContainer extends React.Component {
  static propTypes = { ...ServerLogging.propTypes }
  static defaultProps = { ...ServerLogging.defaultProps }

  render() {
    const serverLoggingProps = {
      ...pick({ ...this.props, ...this.state }, Object.keys(ServerLogging.propTypes)),
    };
    return (
      <div>
        <ServerLogging {...serverLoggingProps} />
      </div>
    );
  }
}

const mapStatesToProps = state => ({
  ...state.serverLoggingReducer,
});

const mapDispatchToProps = dispatch => bindActionCreators({
  restartSplunk,
  loadUserRoles,
}, dispatch);

export default connect(
  mapStatesToProps,
  mapDispatchToProps,
)(ServerLoggingContainer);



