import React from 'react';
import { pick } from 'lodash';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { loadEmailSettings } from '../../actions/settings/EmailSettingsRetrieve';
import { updateEmailSettings } from '../../actions/settings/EmailSettingsUpdate';
import EmailSettings from '../../components/settings/EmailSettings';

class EmailSettingsContainer extends React.Component {
  static propTypes = { ...EmailSettings.propTypes }
  static defaultProps = { ...EmailSettings.defaultProps }

  constructor(props) {
    super(props);

    this.state = {
      mailserver: this.props.initialMailserver,
      authUsername: this.props.initialUsername,
      authPassword: this.props.authPassword,
      confirmPassword: this.props.confirmPassword,
      securityMode: this.props.initialMode,
    };
  }

  componentDidMount() {
    this.props.onLoadFetchEmailInfo();
  }

  componentWillReceiveProps(nextProps) {
    this.setState({
      mailserver: nextProps.initialMailserver,
      authUsername: nextProps.initialUsername,
      securityMode: nextProps.initialMode,
    });
  }

  onUpdateEmailSettings = (name, value) => {
    this.setState({ [name]: value });
  }

  onSubmitEmailChanges = () => {
    this.props.updateEmailSettings(
      this.state.authUsername,
      this.state.authPassword,
      this.state.mailserver,
      this.state.securityMode,
    );
  }

  render() {
    const emailSettingsProps = {
      ...pick({ ...this.props, ...this.state }, Object.keys(EmailSettings.propTypes)),
      ...{
        onUpdateEmailSettings: this.onUpdateEmailSettings,
        onSubmitEmailChanges: this.onSubmitEmailChanges,
      },
    };
    return (
      <div>
        <EmailSettings {...emailSettingsProps} />
      </div>
    );
  }
}

const mapStatesToProps = state => ({
  ...state.emailSettingsReducer,
});

const mapDispatchToProps = dispatch => bindActionCreators({
  onLoadFetchEmailInfo: loadEmailSettings,
  updateEmailSettings,
}, dispatch);

export default connect(
  mapStatesToProps,
  mapDispatchToProps,
)(EmailSettingsContainer);



