import { put, takeLatest, call } from 'redux-saga/effects';
import * as emailAPI from '../services/emailSettings';
import { EMAIL_SETTINGS_RETRIEVE, EMAIL_SETTINGS_UPDATE } from '../actions/actionTypes';
import { emailSettingsRetrieveTracker } from '../actions/settings/EmailSettingsRetrieve';
import { emailSettingsUpdateTracker } from '../actions/settings/EmailSettingsUpdate';

/**
* Helper function to parse the response from the alert_actions email 
*/
function parseEmailSettingsResponse(response) {
  if (response && response.entry) {
    return {
      mailserver: response.entry[0].content.mailserver,
      authUsername: response.entry[0].content.auth_username,
      useSSL: response.entry[0].content.use_ssl,
      useTLS: response.entry[0].content.use_tls,
    };
  }
  return {};
}

export function* retrieveEmailSettings() {
  yield put(emailSettingsRetrieveTracker.start());
  try {
    const { response } = yield call(emailAPI.retrieveEmailSettings);
    const emailInfo = parseEmailSettingsResponse(response);
    yield put(emailSettingsRetrieveTracker.success(emailInfo));
  } catch (error) {
    // Not passing the error, still load the page with desfault setting
    yield put(emailSettingsRetrieveTracker.failure());
  }
}

export function* updateEmailSettings(action) {
  yield put(emailSettingsUpdateTracker.start());
  try {
    const { response } = yield call(emailAPI.updateEmailSettings, action);
    const emailInfo = parseEmailSettingsResponse(response);
    yield put(emailSettingsUpdateTracker.success(emailInfo));
  } catch (error) {
    yield put(emailSettingsUpdateTracker.failure(error));
  }
}

export default function* emailSaga() {
  yield takeLatest(EMAIL_SETTINGS_RETRIEVE, retrieveEmailSettings);
  yield takeLatest(EMAIL_SETTINGS_UPDATE, updateEmailSettings);
}




