import EMUtils from '../libs/EMUtils';
import { requestAPI } from './util';

const BASE_URL = EMUtils.getAppSplunkdUrl('admin/alert_actions/email');

/**
 * Perform GET on aws account endpoint
 *
 * @returns - Ajax promise
 */
export const retrieveEmailSettings = () => requestAPI(BASE_URL, '', 'GET');

/**
 * Destructure action and POST to requestAPI with account data
 *
 * @param action with attributes
 * @returns - Ajax promise
 */
export const updateEmailSettings = action => requestAPI(BASE_URL, '', 'POST', action);



