import React from 'react';
import { pick } from 'lodash';
import { bindActionCreators } from 'redux';
import { connect } from 'react-redux';
import { loadLicense } from '../../actions/licenses/LicenseRetrieve';
import {
  resetLicenseUpload,
  uploadLicense,
} from '../../actions/licenses/LicenseUpload';
import { getLicenseUsage, getDailyLicenseUsage } from '../../actions/licenses/LicenseUsage';
import Licensing from '../../components/settings/Licensing';

class LicensingContainer extends React.Component {
  static propTypes = { ...Licensing.propTypes }
  static defaultProps = { ...Licensing.defaultProps }

  componentDidMount() {
    this.props.onLoadLicense();
    this.props.onGetLicenseUsage();
    this.props.onGetDailyLicenseUsage();
  }

  componentWillReceiveProps(nextProps) {
    this.setState({
      licenseName: nextProps.licenseName,
      retentionVol: nextProps.retentionVol,
      expiredDate: nextProps.expiredDate,
      chartData: nextProps.chartData,
      dailyChartData: nextProps.dailyChartData,
      sumChartDate: nextProps.sumChartData,
      dailyUsage: nextProps.dailyUsage,
    });
  }

  onUploadLicenseFile = (filename, value) => {
    this.props.onUploadLicense(filename, value);
  }

  render() {
    const licensingProps = {
      ...pick({ ...this.props, ...this.state }, Object.keys(Licensing.propTypes)),
      ...{
        onResetLicenseUpload: this.props.onResetLicenseUpload,
        onUploadLicenseFile: this.onUploadLicenseFile,
      },
    };
    return (
      <div>
        <Licensing {...licensingProps} />
      </div>
    );
  }
}

const mapStatesToProps = state => ({
  ...state.licenseReducer,
});

const mapDispatchToProps = dispatch => bindActionCreators({
  onLoadLicense: loadLicense,
  onUploadLicense: uploadLicense,
  onGetLicenseUsage: getLicenseUsage,
  onGetDailyLicenseUsage: getDailyLicenseUsage,
  onResetLicenseUpload: resetLicenseUpload,
}, dispatch);

export default connect(
  mapStatesToProps,
  mapDispatchToProps,
)(LicensingContainer);



